  methods: {
    changeSelect: function () {
      if (this.value === "") {
        console.log('a');
        this.dialogVisible = true
      }
      console.log(this.value);
      console.log(this.onTaped);
    },
    checkProcess() {
      console.log(this.onTaped);
      if (!this.timeFlag) {
        // 启动定时器
        this.timeFlag = true;
        this.timeRefresh = setInterval(() => {
          this.allSecond += 1;
        }, 1000);
      }
      let flag = 0;
      for (let index = 0; index < this.onTaped.length; index++) {
        const element = this.onTaped[index];
        if (element === this.value[index]) {
          flag += 1;
        }
        this.correct = (flag / this.onTaped.length).toFixed(2);
        if (index < this.value.length - 1) {
          this.next = this.value[index + 1];
        } else if (index == this.value.length - 1) {
          window.clearInterval()
          alert(`速度:${(this.onTaped.length / this.allSecond * 60).toFixed(0)}字/分,正确率:${this.correct*100} %,用时: ${this.allSecond}`);
        }
      }
